import React, {useState} from 'react';
import {View, Image, Text, ScrollView} from 'react-native';
import "react-image-gallery/styles/css/image-gallery.css";

import sharedStyles from '../../Styles/Shared/style';

import { InfoModal } from '../Modals/InfoModal';
import { WaitingModal } from '../Modals/WaitingModal';

import {uri, getDecryptedUsername, getDecryptedToken} from '../Globals/';

import BackArrow from './BackArrow';
import DashboardHeader from './DashboardHeader';

import TraineeLogIcon from '../../assets/icons/trainee.png';

import TraineeLogCard from './TraineeLogCard';

// import { getTraineesRequests } from '../actions';


function TraineeLogs(props){

    const [waitingModalVisible, setWaitingModalVisible]   = useState(false);
    const [InfoModalState, setInfoModalState]             = useState(false);
    const [infoMsg, setInfoMsg]                           = useState("");

    const [fetchTraineesLogs, setFetchTraineesLogs] = useState(true);
    //temp

    // {
    //     id: 0,
    //     name: "   ",
    //     image: "/media/institution_profile_images/icons8-ruby-gem-480_tAPKLYK.png",
    //     gender: "male",
    //     birthdate: "// ",
    //     languages: "  ",
    //     phone: "+2499123456789",
    //     email: "basel22@gmail.com",
    //     acceptDate: "2022/3/22 3:00"
    // }
    const [traineesLogs, setTraineesLogs] = useState([]);

    const getTraineesLogsAction =  async (username, token) => {
            // let data          = {};
        
            var headerData = new Headers();

            headerData.append("Authorization", `Token ${token}`);


            var requestOptions = {
                method: 'GET',
                headers: headerData,
                redirect: 'follow'
            };



            const response = await fetch(`${uri}/api/institution/getTraineesLogs/${username}/`, requestOptions)
            .then( async (response) => {
                if(response.status === 200){
                    let parsed_data     = await JSON.parse((await JSON.stringify( await response.json())));
                    setTraineesLogs(parsed_data);
                    // data["status"]      = 200;
                    // data["data"]        = ;
                }else if(response.status === 404){
                    setInfoModalState(true);
                    setInfoMsg("   !");
                    // data["status"]      = 404;
                    // data["responseMsg"] = "   !";
                }
            }).catch(err => {
                setInfoModalState(true);
                setInfoMsg("  !");
                // data["status"]      = 408;
                // data["responseMsg"] = ";
            });

    };

    const processLanguages = (languages) => {
        let languages_full_string = "";

        languages.map((language) => {
            languages_full_string = languages_full_string + language.language;
        })

        return languages_full_string;
    }

    const processprofileImage = (image) => {
        var temp_image = image.slice(6)

        return temp_image;
    }

    const getTraineesLogsList = () => {

        return traineesLogs.map((log, key)=>(

            <TraineeLogCard key={key} id={log.id} name={log.name} gender={log.gender}
                image={processprofileImage(log.image)} birthdate={log.birthdate} 
                languages={processLanguages(log.languages)}
                phone={log.phone} email={log.email}
                acceptDate={log.acceptDate} />
        ));
    }
   
    const getTraineesLogs = () => {
        if(fetchTraineesLogs){
            setFetchTraineesLogs(false);
            getTraineesLogsAction(getDecryptedUsername(), getDecryptedToken());
        }else{
            return getTraineesLogsList();
        }
    }
      
    

    return(

        <ScrollView style={{width: '100%'}}>
             <InfoModal message={infoMsg} modalVisible={InfoModalState} 
            setModalVisible={setInfoModalState} />

            {
                waitingModalVisible === true ?
                    <WaitingModal />
                :
                    null
            }

            <View style={{width: '100%'}}>
                <DashboardHeader />
            </View>

            <View style={sharedStyles.viewsTitle}>

       

                <View style={{width: 120, marginRight: 20}}>
                    <BackArrow />
                </View> 

                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{width: "100%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>

                            <Image style={{width: 30, height: 30}} />
                            <Text style={sharedStyles.viewsTitleText}>    </Text>
                            <Image style={{width: 30, height: 30, marginRight: 20}} source={TraineeLogIcon} />

                        </View>
                    </View>
                </View>

            </View>

            

            <View style={{margin: 20}}>
                {getTraineesLogs()}
            </View>
            

        </ScrollView>

    );
}

export default TraineeLogs;