import React, {useState, useEffect} from 'react';
import {View, Image, Text, ScrollView} from 'react-native';
import "react-image-gallery/styles/css/image-gallery.css";

import sharedStyles from '../../Styles/Shared/style';

import { InfoModal } from '../Modals/InfoModal';
import { WaitingModal } from '../Modals/WaitingModal';

import {uri, getDecryptedUsername, getDecryptedToken} from '../Globals/';

import BackArrow from './BackArrow';
import DashboardHeader from './DashboardHeader';

import TraineeRequestsIcon from '../../assets/icons/trainees_requests.png';

import RequestCard from './RequestCard';

// import { getTraineesRequests } from '../actions';

import {connect} from 'react-redux';

function TraineeRequests(props){

    const [waitingModalVisible, setWaitingModalVisible]   = useState(false);
    const [InfoModalState, setInfoModalState]             = useState(false);
    const [infoMsg, setInfoMsg]                           = useState("");

    const [fetchTraineeRequests, setFetchTraineeRequests] = useState(true);
    //temp
    const [traineesRequests, setTraineesRequests] = useState([]);

    // const updateStates = () => {

    //     if(props.state.data !== undefined) {

    //         setWaitingModalVisible(false);
    //         if(props.state.data["status"] === 200){
    //             setTraineesRequests(props.state.data["data"]);

    //         }else if(props.state.data.token_response_data.status === 404){

    //             setInfoModalMessage(props.state.data["responseMsg"]);
    //             setInfoModalVisible(true);

    //         }else if(props.state.data.token_response_data.status === 408){

    //             setInfoModalMessage(props.state.data["responseMsg"]);
    //             setInfoModalVisible(true);
    //         }
    //     }
    // }


    // useEffect(()=>{
    //     updateStates();
    // });

    const getTraineesRequestsAction =  async (username, token) => {
            // let data          = {};
        
            var headerData = new Headers();

            headerData.append("Authorization", `Token ${token}`);


            var requestOptions = {
                method: 'GET',
                headers: headerData,
                redirect: 'follow'
            };



            const response = await fetch(`${uri}/api/institution/getTraineesRequests/${username}/`, requestOptions)
            .then( async (response) => {
                if(response.status === 200){
                    let parsed_data     = await JSON.parse((await JSON.stringify( await response.json())));
                    setTraineesRequests(parsed_data);
                    // data["status"]      = 200;
                    // data["data"]        = ;
                }else if(response.status === 404){
                    setInfoModalState(true);
                    setInfoMsg("   !");
                    // data["status"]      = 404;
                    // data["responseMsg"] = "   !";
                }
            }).catch(err => {
                setInfoModalState(true);
                setInfoMsg("  !");
                // data["status"]      = 408;
                // data["responseMsg"] = ";
            });

    };


    const processImages = (images) => {
        var processedImages = [];
        images.map((image)=>{
            let processed_image = uri + "/media/" + image.image;

            let new_image = {
                "thumbnail": processed_image,
                "original": processed_image,
                "thumbnailWidth": 300,
                "thumbnailHeight": 100
            }

            processedImages.push(new_image);
        });
        return processedImages;
    }

    const removeRequestCard = (requirementID) => {
        var requirementIndex = null;
        traineesRequests.map((requirement, index)=>{
            if(requirement.requirementID == requirementID){
                requirementIndex = index;
                return;
            }
        });
        traineesRequests.splice(requirementIndex, 1);
        setTraineesRequests(traineesRequests);
    }
    
    const acceptRequest = async (OrderID, requirementID, traineeName) => {

        setWaitingModalVisible(true);

        var headers = new Headers();
        headers.append("Authorization", `Token ${getDecryptedToken()}`);

        try{

        
            var requestOptions = {
                method: "GET",
                headers: headers,
                redirect: 'follow'
            };


            const response = await fetch(`${uri}/api/institution/acceptTraineeRequest/${getDecryptedUsername()}/${OrderID}/${requirementID}/`,
                            requestOptions)
                            .then((response)=>{
                                if(response.status === 200){
                                    setWaitingModalVisible(false);
                                    setInfoModalState(true);
                                    setInfoMsg(`
                                    \n     
                                    \n ${traineeName}
                                    \n !
                                    \n           `);
                                    removeRequestCard(requirementID);
                                }else if(response.status === 404){
                                    setWaitingModalVisible(false);
                                    setInfoModalState(true);
                                    setInfoMsg("     !");
                                    removeRequestCard(requirementID);
                                }
                            }).catch((error) => {
                                setWaitingModalVisible(false);
                                setInfoModalState(true);
                                setInfoMsg("   !");
                            });
        }catch(e){
            console.log(e);
        }
    }

    const rejectRequest = async (OrderID, requirementID, traineeName) => {
        setWaitingModalVisible(true);

        var headers = new Headers();
        headers.append("Authorization", `Token ${getDecryptedToken()}`);

        try{

        
            var requestOptions = {
                method: "GET",
                headers: headers,
                redirect: 'follow'
            };


            const response = await fetch(`${uri}/api/institution/rejectTraineeRequest/${getDecryptedUsername()}/${OrderID}/${requirementID}/`,
                            requestOptions)
                            .then((response)=>{
                                if(response.status === 200){
                                    setWaitingModalVisible(false);
                                    setInfoModalState(true);
                                    setInfoMsg(`
                                    \n      
                                    \n${traineeName}
                                    \n ! `);
                                    removeRequestCard(requirementID);
                                }else if(response.status === 404){
                                    setWaitingModalVisible(false);
                                    setInfoModalState(true);
                                    setInfoMsg("     !");
                                    removeRequestCard(requirementID);
                                }
                            }).catch((error) => {
                                setWaitingModalVisible(false);
                                setInfoModalState(true);
                                setInfoMsg("   !");
                            });
        }catch(e){
            console.log(e);
        }
    }


    const getTraineesRequestsList = () => {
        return traineesRequests.map((request, key)=>(

            <RequestCard key={key} orderId={request.orderId} orderTitle={request.orderTitle}
                orderLogo={request.orderLogo}     orderExpiry={request.orderExpiry}
                requirementId={request.requirementId} traineeId={request.traineeId}
                traineeName={request.traineeName}
                trainee_images={processImages(request.trainee_images)}
                traineeAttachments={request.traineeAttachments}
                acceptRequest={acceptRequest} rejectRequest={rejectRequest} />
        ));
    }
   
    const getTraineesRequests = () => {
        if(fetchTraineeRequests){
            setFetchTraineeRequests(false);
            getTraineesRequestsAction(getDecryptedUsername(), getDecryptedToken());
        }else{
            return getTraineesRequestsList();
        }
    }
      
    

    return(

        <ScrollView style={{width: '100%'}}>
             <InfoModal message={infoMsg} modalVisible={InfoModalState} 
            setModalVisible={setInfoModalState} />

            {
                waitingModalVisible === true ?
                    <WaitingModal />
                :
                    null
            }

            <View style={{width: '100%'}}>
                <DashboardHeader />
            </View>

            <View style={sharedStyles.viewsTitle}>

       

                <View style={{width: 120, marginRight: 20}}>
                    <BackArrow />
                </View> 

                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{width: "100%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>

                            <Image style={{width: 30, height: 30}} />
                            <Text style={sharedStyles.viewsTitleText}>    </Text>
                            <Image style={{width: 30, height: 30, marginRight: 20}} source={TraineeRequestsIcon} />

                        </View>
                    </View>
                </View>

            </View>

            

            <View style={{margin: 20}}>
                {getTraineesRequests()}
            </View>
            

        </ScrollView>

    );
}

const mapStateToProps = (state) => {
    return {
        state: state.getTraineesRequests
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getTraineesRequests: (username, token) => {
            dispatch(getTraineesRequests(username, token))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TraineeRequests);