import React, {useState, useEffect} from 'react';
import {View, Text, Image, TouchableOpacity, TextInput} from 'react-native';

import Header from './Components/Header';
import BackArrow from './Components/BackArrow';



import signUpStyles from '../Styles/SignUp/style';
import sharedStyles from '../Styles/Shared/style';

import emailIcon from '../assets/icons/signup_email.png';
import institIcon from '../assets/icons/icons8-organization-100.png';
import numberIcon from '../assets/icons/signup_number.png';
import pencilIcon from '../assets/icons/login_signup2.png';
import userIcon from '../assets/icons/login_avatar_3.png';
import domainTypeIcon from '../assets/icons/signup_domain_type.png';
import addressIcon from '../assets/icons/signup_address.png';
import keyIcon from '../assets/icons/login_key2.png';
import showPasswordIcon from '../assets/icons/signup_opendEye.png';
import hidePasswordIcon from '../assets/icons/signup_closedEye.png';

import {InfoModal} from './Modals/InfoModal';
import {WaitingModal} from './Modals/WaitingModal';

import {registerInstitution} from './actions/';
import {connect} from 'react-redux';







function SignUp (props) {

    const [waitingModalVisible, setWaitingModalVisible] = useState(false);
    const [goToDashboard, setGoToDashboard]             = useState(false);
    const [InfoModalState, setInfoModalState]           = useState(false);
    const [infoMsg, setInfoMsg]                         = useState("");


    const [showPassword, setShowPassword] = useState(false);
    const [name, setName]                 = useState("");
    const [username, setUsername]         = useState("");
    const [phone, setPhone]               = useState("");
    const [taxID, setTaxID]               = useState("");
    const [address, setAddress]           = useState("");
    const [domainType, setDomainType]     = useState("");
    const [email, setEmail]               = useState("");
    const [password, setPassword]         = useState("");
    const [password2, setPassword2]       = useState("");


    const checkGoTodDashboard = () => {
        if(goToDashboard){
            props.navigation.navigate("Login");
        }
    }


 
    const updateStates = () => {
        if(props.state.data.status === 200){
            setWaitingModalVisible(false);
            let msg = `
                      
            .        `;
            setInfoMsg(msg);
            setInfoModalState(true);
            props.state.data.status = "";
            return;
        }
        if(props.state.data.status === 422){
            setWaitingModalVisible(false);
            setInfoMsg(props.state.data.responseMsg["responseMsg"]);
            setInfoModalState(true);

            props.state.data.status = "";
            return;
        }

        if(props.state.data.responseMsg !== undefined) {
            setWaitingModalVisible(false);
            
            const responseMsg = props.state.data.responseMsg;
            let info = "";
            let counter = 1;
            for (let key in responseMsg) {
                if(responseMsg[key][0].includes("email")) {
                    info = info + "\n" + `${counter++}-     !`;
                    continue;
                }
                info = info + "\n" + `${counter++}- ` + responseMsg[key][0];
            }
            setInfoMsg(info);
            setInfoModalState(true);
            props.state.data.responseMsg = undefined;
        }
    }

    const validate = () => {
        if(name.length < 3){
            setInfoMsg("         ");
            setInfoModalState(true);
            return;
        }else if(username.length < 3){
            setInfoMsg("         ");
            setInfoModalState(true);
            return;
        }else if(!email.match("[a-zA-Z0-9._][@][a-zA-Z.]+[.][a-zA-Z]+")){
            setInfoMsg("    ");
            setInfoModalState(true);
        }else if(phone.length === 0){
            setInfoMsg("   !");
            setInfoModalState(true);
            return;
        }else if(domainType.length === 0){
            setInfoMsg("   !");
            setInfoModalState(true);
            return;
        }else if(address.length === 0){
            setInfoMsg("  !");
            setInfoModalState(true);
        }else if(password.length === 0 || password2.length === 0){
            setInfoMsg("   !");
            setInfoModalState(true);
            return;
        }else if(password !== password2){
            setInfoMsg("  !");
            setInfoModalState(true);
            return;
        }else if(taxID.length === 0){
            setInfoMsg("    !");
            setInfoModalState(true);
            return;
        }

        setWaitingModalVisible(true);
        props.registerInstitution(name, username, email, phone, password, address, taxID, domainType);
    }

    useEffect(() => {
        updateStates();
        checkGoTodDashboard();
    });


    return(
        
        <View style={signUpStyles.view}>


            <InfoModal message={infoMsg} modalVisible={InfoModalState} 
            setModalVisible={setInfoModalState} setGoToDashboard={setGoToDashboard} />

            {
                waitingModalVisible === true ?
                    <WaitingModal />
                :
                    null
            }

            {/* header */}
            <View style={{width: '100%'}}>
                <Header />
            </View>
            


            <View style={{flexGrow: 1, width: 120, marginTop: 100, position: 'absolute'}}>
                <BackArrow />
            </View>

            {/* card */}
            <View style={[signUpStyles.cardBackLayer, {alignSelf: 'center', marginTop: -60}]}>
                <View style={signUpStyles.cardFrontLayer}>

                        {/* title */}
                        <Text style={sharedStyles.titleText}>  </Text>
                        {/* body */}
                    <View style={[signUpStyles.body, {flexDirection: 'column', marginTop: 40}]}>

                        <View style={{width: '70%', alignSelf: 'center'}}>
                            <View style={sharedStyles.textInputBackLayer}>
                                <View style={sharedStyles.textInputFrontLayer}>
                                    <Image />
                                    <TextInput placeholder=" " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                        autoCapitalize={'none'} autoComplete={'off'} onChangeText={(value)=>setName(value)} />
                                    <Image source={institIcon} style={sharedStyles.icon} />
                                </View>
                            </View>
                        </View>


                        <View style={{width: '100%', flexGrow: 1, flexDirection: 'row'}}>

                                {/* body left column */}
                                <View style={signUpStyles.bodyColumn}>

                                    {/* domain type */}
                                    <View style={sharedStyles.textInputBackLayer}>
                                        <View style={sharedStyles.textInputFrontLayer}>
                                            <Image />
                                            <TextInput placeholder="  " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                                autoCapitalize={'none'} autoComplete={'off'} onChangeText={(value)=>setDomainType(value)} />
                                            <Image source={domainTypeIcon} style={sharedStyles.icon} />
                                        </View>
                                    </View>


                                    {/* email text input */}
                                    <View style={sharedStyles.textInputBackLayer}>
                                        <View style={sharedStyles.textInputFrontLayer}>
                                            <Image />
                                            <TextInput placeholder=" " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                                autoCapitalize={'none'} autoComplete={'off'} onChangeText={(value)=>setEmail(value)}/>
                                            <Image source={emailIcon} style={sharedStyles.icon} />
                                        </View>
                                    </View>

                                    {/* password text input */}
                                    <View style={sharedStyles.textInputBackLayer}>
                                        <View style={sharedStyles.textInputFrontLayer}>
                                            {
                                                showPassword ?
                                                    <TouchableOpacity onPress={()=>{setShowPassword(!showPassword)}}>
                                                        <Image source={showPasswordIcon} style={sharedStyles.iconRight} />
                                                    </TouchableOpacity>
                                                :
                                                    <TouchableOpacity onPress={()=>{setShowPassword(!showPassword)}}>
                                                        <Image source={hidePasswordIcon} style={sharedStyles.iconRight} />
                                                    </TouchableOpacity>
                                            }
                                            
                                            <TextInput placeholder=" " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                                autoCapitalize={'none'} secureTextEntry={!showPassword} onChangeText={(value)=>setPassword(value)} autoComplete={'off'} />
                                            <Image source={keyIcon} style={sharedStyles.icon} />
                                        </View>
                                    </View>
                                    <View style={sharedStyles.textInputBackLayer}>
                                        <View style={sharedStyles.textInputFrontLayer}>
                                            <Image /><Image /><Image />
                                            <TextInput placeholder="   " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                                autoCapitalize={'none'} secureTextEntry={!showPassword} onChangeText={(value)=>setPassword2(value)} autoComplete={'off'} />
                                            <Image source={keyIcon} style={sharedStyles.icon} />
                                        </View>
                                    </View>



                                </View>
                                {/*  end of body left column */}


                                
                                {/* body right column */}
                                <View style={signUpStyles.bodyColumn}>

                                    <View style={sharedStyles.textInputBackLayer}>
                                        <View style={sharedStyles.textInputFrontLayer}>
                                            <Image />
                                            <TextInput placeholder="  (   )" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                                autoCapitalize={'none'} autoComplete={'off'} onChangeText={(value)=>setUsername(value)} />
                                            <Image source={userIcon} style={sharedStyles.icon} />
                                        </View>
                                    </View>

                                    <View style={sharedStyles.textInputBackLayer}>
                                        <View style={sharedStyles.textInputFrontLayer}>
                                            <View style={signUpStyles.phoneContainer}>
                                                <Text style={{fontSize: 18, color: '#707070'}}>+249    | </Text>
                                                <TextInput placeholder=" ( )" placeholderTextColor={'#C7C7CD'} style={signUpStyles.textInputPhone} 
                                                    autoCapitalize={'none'} autoComplete={'off'} numeric={true}  maxLength={9} keyboardType="numeric" onChangeText={(value)=>setPhone(value)}/>
                                            </View>
                                            <Image source={numberIcon} style={[sharedStyles.btnIcon, {marginRight: 20}]} />
                                        </View>
                                    </View>

                                    <View style={sharedStyles.textInputBackLayer}>
                                        <View style={sharedStyles.textInputFrontLayer}>
                                            <Image />
                                            <TextInput placeholder="  " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                                autoCapitalize={'none'} autoComplete={'off'} onChangeText={(value)=>setTaxID(value)}/>
                                            <Image source={numberIcon} style={sharedStyles.btnIcon} />
                                        </View>
                                    </View>

                                    <View style={sharedStyles.textInputBackLayer}>
                                        <View style={sharedStyles.textInputFrontLayer}>
                                            <Image />
                                            <TextInput placeholder="" placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                                autoCapitalize={'none'} autoComplete={'off'} onChangeText={(value)=>setAddress(value)}/>
                                            <Image source={addressIcon} style={sharedStyles.icon} />
                                        </View>
                                    </View>
                                </View>
                                {/* end of body right column */}


                            </View>
                        </View>

                            {/* signup btn */}
                        <View style={{width: '40%', marginTop: 12, alignSelf: 'center'}}>
                            <View style={sharedStyles.buttonBackLayer}>
                                <TouchableOpacity style={sharedStyles.buttonFrontLayer} onPress={()=>{validate()}}>
                                    <Image /><Image source={pencilIcon} style={sharedStyles.btnIcon} />
                                    <Text style={sharedStyles.textBtn}></Text>
                                    <Image /><Image />
                                </TouchableOpacity>
                            </View>
                        </View>
                    </View>
                </View>

        </View>

    );


}


const mapStateToProps = (state) => {
    return {
        state: {
            data: state.InstitutionRegisteration
        }
    }
}


const mapDispatchToProps = (dispatch) =>{
    return {
        registerInstitution: (name, username, email, phone, password, address, taxID, domainType) => {
            dispatch(registerInstitution(name, username, email, phone, password, address, taxID, domainType))
        },
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(SignUp);