import React, {useState} from 'react';
import {View, Text, TouchableOpacity, Image} from 'react-native';
import sharedStyles from '../../Styles/Shared/style';

import ImageGallery from 'react-image-gallery';

import notification_info from '../../assets/icons/notification_info_circle.png';


import {useNavigation} from '@react-navigation/native';

import { setIdForOrderCard, uri, logo } from '../Globals/index';
import requestOrder from '../../Styles/RequestOrder/style';


import submitCancelIcon from '../../assets/icons/submitCancel.png';
import submitConfirmIcon from '../../assets/icons/submitConfirm.png';
import showLessIcon from '../../assets/icons/showLess.png';
import expandMoreIcon from '../../assets/icons/expandMore.png';



export default function RequestCard(props){


    const navigation        = useNavigation();

    const [name, setName] = useState(props.traineeName);
    const [orderTitle, setOrderTitle] = useState(props.orderTitle);
    const [date, setDate]   = useState(props.orderExpiry.split('T')[0] + " " + props.orderExpiry.split('T')[1]);
    const [id, setID]       = useState();

    const [dropdown, setDropdown] = useState(false)

    return(
        <View style={{marginTop: 20}}>
            <View style={[sharedStyles.textInputBackLayer, {width: "75%", alignSelf: 'center'}]}>
                <View style={sharedStyles.textInputFrontLayer}>
                    <View style={{flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between', padding: 20}}>
                    
                        <View style={{flexGrow: 1, flexDirection: 'column', justifyContent: 'space-between'}}>
                            <Image source={`${uri}/media/${props.orderLogo}`} style={requestOrder.logo} />
                            <Text style={[requestOrder.cardTitle, {marginTop: 100,
                                color: '#707070', fontSize: 15}]}>{date}      </Text>
                        </View>
                        
                        
                        <View style={{alignItems: 'center', alignSelf: 'center'}}>
                            <Text style={[requestOrder.cardTitle, {fontSize: 20, color: '#EF8630'}]}>{name}</Text>
                        </View>

                        <View style={{flexGrow: 1, flexDirection: 'column', justifyContent: 'space-between'}}>
                            <Text style={[requestOrder.cardTitle, {fontSize: 20}]}>{orderTitle}</Text>
                            <View style={requestOrder.OrdersEditButtonBackLayer}>
                                {
                                    dropdown === false ?
                                        <TouchableOpacity style={[sharedStyles.textInputFrontLayer, {alignItems: 'center', alignSelf:'center'}]} onPress={()=>{setDropdown(!dropdown)}} >
                                            <Image style={{width: 70.00, height: 35.00, padding: 20}} source={expandMoreIcon} />
                                            <Text style={requestOrder.cardButtonTitleText}>{' '}</Text>
                                        </TouchableOpacity>
                                    :
                                        <TouchableOpacity style={[sharedStyles.textInputFrontLayer, {alignItems: 'center', alignSelf:'center'}]} onPress={()=>{setDropdown(!dropdown)}} >
                                            <Image style={{width: 70.00, height: 35.00, padding: 20}} source={showLessIcon} />
                                            <Text style={requestOrder.cardButtonTitleText}>{' '}</Text>
                                        </TouchableOpacity>
                                }
                            </View>
                        </View>

                    </View>
                </View>
                {   
                    dropdown === true ?
                        <View style={{flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between', backgroundColor: '#F2F2F2', padding: 20}}>
                            <View style={{maxWidth: 300}}>
                                {
                                    props.traineeAttachments !== ''  ?
                                        <ImageGallery items={props.trainee_images} autoPlay={false} infinite={true} />
                                    :
                                        <Text style={requestOrder.noImagesTitle}>  !</Text>
                                }
                                
                            </View>
                            <View style={{flexGrow: 1, maxWidth: '60%', flexDirection: 'column', 
                            justifyContent: 'space-around'}}>
                                <View>
                                    <Text style={requestOrder.subTitleText}></Text>
                                </View>
                                <View>
                                    <Text style={requestOrder.attachmentTitleText}>
                                        {
                                            props.traineeAttachments === '' ?
                                                "  !"
                                            :
                                                props.traineeAttachments
                                        }
                                        </Text>
                                </View>

                                <View style={{flexDirection: 'row', justifyContent: 'space-around'}} >

                                    <View style={[sharedStyles.buttonBackLayer, {maxWidth: '40%'}]}>
                                        <TouchableOpacity style={sharedStyles.buttonFrontLayer}
                                             onPress={()=>{props.acceptRequest(props.orderId, props.requirementId, props.traineeName)}}>
                                            <View style={{padding: 5, flexDirection: 'row', justifyContent: 'space-around', alignItems: 'center'}}>
                                                <Image source={submitConfirmIcon} style={{width: 32.55, height: 23.5}} />
                                                <Text style={sharedStyles.textBtn}> </Text>
                                            </View>
                                        </TouchableOpacity>
                                    </View>

                                    <View style={[sharedStyles.buttonBackLayer, {maxWidth: '40%'}]}>
                                        <TouchableOpacity style={sharedStyles.buttonFrontLayer} onPress={()=>{props.rejectRequest(props.orderId, props.requirementId, props.traineeName)}}>
                                            <View style={{padding: 5, flexDirection: 'row', justifyContent: 'space-around', alignItems: 'center'}}>
                                                <Image source={submitCancelIcon} style={{width: 27.3, height: 27.3}} />
                                                <Text style={sharedStyles.textBtn}> </Text>
                                            </View>
                                        </TouchableOpacity>
                                    </View>

                                </View>
                            </View>
                        </View>
                    :
                        null
                }
            </View>
        </View>
    );
}